.itemListContainer{
    background-color: #f1f1f1;
    width: 100%;
    min-height: 100vh;
    display: flex;
    box-sizing: border-box;
    padding: 1rem;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 8.5rem;
}

.itemListContainer *{
    text-decoration: none;
}